<template>
  <div class="fill-height">
    <app-bar title="Configurações">
      <template v-slot:append>
        <v-btn icon to="/" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <v-list outlined :dark="$store.state.dark" class="py-0">
      <v-list-item @click="$store.commit('setDark', !$store.state.dark)">
        <v-list-item-icon>
          <v-icon v-if="$store.state.dark">mdi-white-balance-sunny</v-icon>
          <v-icon v-else>mdi-weather-night</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>Modo escuro</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list-item @click="dialogPassword = true">
        <v-list-item-icon>
          <v-icon>mdi-lock</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>Alterar senha</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list-item @click="$store.dispatch('session/destroy')">
        <v-list-item-icon>
          <v-icon>mdi-connection</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>Desconectar da sua conta</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-footer app :color="$store.state.dark ? undefined : 'primary'" dark class="justify-space-between">
      <span>&copy; iCorretor</span>
      <span>versão {{version}}</span>
    </v-footer>
    <v-dialog width="400" max-width="94%" v-model="dialogPassword">
      <v-card>
        <v-form @submit.prevent="submitPassword" ref="form-password">
          <v-card-title>Alteração de senha</v-card-title>
          <v-card-text>
            <v-text-field
              label="Senha atual"
              placeholder="Digite a senha atual"
              autocomplete="current-password"
              autocapitalize="off"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              :type="showPassword ? 'text' : 'password'"
              @click:append="showPassword = !showPassword"
              outlined
              v-model="iptCurrentPass"
              :rules="rules.required"
              :disabled="loadingPassword"
            ></v-text-field>
            <v-text-field
              label="Nova senha"
              placeholder="Digite a nova senha"
              autocomplete="new-password"
              autocapitalize="off"
              :append-icon="showPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="showPassword2 ? 'text' : 'password'"
              @click:append="showPassword2 = !showPassword2"
              outlined
              v-model="iptNewPass"
              :rules="rules.password"
              :disabled="loadingPassword"
            ></v-text-field>
            <v-text-field
              label="Repita a nova senha"
              placeholder="Repita a nova senha"
              autocomplete="off"
              autocapitalize="off"
              :append-icon="showPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="showPassword2 ? 'text' : 'password'"
              @click:append="showPassword2 = !showPassword2"
              outlined
              v-model="iptNewPassRep"
              :rules="passwordRepRule"
              :disabled="loadingPassword"
            ></v-text-field>
          </v-card-text>
          <v-card-actions class="justify-center">
            <v-btn outlined color="primary" type="submit" :loading="loadingPassword">Confirmar</v-btn>
            <v-btn outlined color="secondary" type="button" @click="dialogPassword = false" :disabled="loadingPassword">Cancelar</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AppBar from '@/components/AppBar';
import JsonPackage from '../../package.json';
import AppWebClient from '@/http/AppWebClient';
export default {
  name: 'Config',
  components: {AppBar},
  data: () => ({
    loaded: true,
    version: JsonPackage.version.substr(0, JsonPackage.version.indexOf('.', JsonPackage.version.indexOf('.') + 1)),
    dialogPassword: false,
    iptCurrentPass: '',
    iptNewPass: '',
    iptNewPassRep: '',
    showPassword: false,
    showPassword2: false,
    loadingPassword: false,
    rules: {
      required: [
        v => !!v || 'Digite a senha',
      ],
      password: [
        v => !!v || 'Digite a senha',
        v => (v && v.length >= 4) || 'Precisa ter pelo menos 4 caracteres',
        v => (v && v.length <= 16) || 'O limite é 16 caracteres',
        v => (!/\s/g.test(v)) || 'Espaços não são permitidos',
      ],
    },
  }),
  methods: {
    async submitPassword() {
      if (!this.$refs['form-password'].validate()) return;
      this.loadingPassword = true;
      try {
        const appWebClient = new AppWebClient();
        await appWebClient.alterarSenha(this.iptCurrentPass, this.iptNewPass);
        this.dialogPassword = false;
        this.$snackbar({text: 'Senha alterada', color: 'success'});
        this.$refs['form-password'].reset()
      } finally {
        this.loadingPassword = false;
      }
    },
  },
  computed: {
    passwordRepRule() {
      return [...this.rules.password, v => v === this.iptNewPass || 'A senha não confere com a anterior'];
    },
  },
}
</script>

<style scoped>

</style>